.show {
  margin: 4rem 6rem;
  color: #2a2a2a;
}

.show h1 {
  font-size: 3rem;
}

.showcards {
  margin-top: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.show video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.s-card {
  width: 80%;
  text-align: start;
  box-shadow: 0 5px 25px 2px rgba(0, 0, 0, 0.11);
  border-radius: 7px;
  cursor: pointer;
  padding: 1rem 0.8rem;
  margin-bottom: 4rem;
}

.s-image {
  margin: auto;
  width: 100%;
  height: 400px;
  overflow: hidden;
  border-radius: 7px;
}

.s-image img {
  width: 100%;
  height: 100%;
  transition: 0.3s ease-in-out;
  border-radius: 7px;
}

.s-image:hover img {
  transform: scale(1.2);
}

.s-card h4 {
  font-size: 1.4rem;
  padding: 0.9rem 0 0.2rem 0;
}

h6 {
  font-weight: 400;
  line-height: 1.2;
  color: var(--color-dark);
  margin-bottom: 1rem;
  font-size: 1.5rem;
}

@media screen and (max-width: 850px) {
  .show {
    margin: 4rem 2rem;
  }

  .showcards {
    flex-direction: column;
  }

  .s-card {
    width: 100%;
    margin-bottom: 2rem;
  }

  .s-image {
    width: 100%;
    height: 370px;
    overflow: hidden;
    border-radius: 7px;
  }
}